body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  box-sizing: border-box; }

button {
  cursor: pointer; }

.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto; }
  .App .links-container {
    margin: 10px; }

.Nav_link {
  text-decoration: none;
  color: black; }

.activeRoute {
  color: red; }

.ContactPage {
  max-width: 500px;
  margin: 0 auto; }
  .ContactPage .top-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around; }

.ContactFilter input {
  padding: 5px;
  margin: 20px;
  border-radius: 2px; }

.ContactList {
  max-width: 500px;
  margin: 0 auto;
  list-style: none; }
  .ContactList .link {
    text-decoration: none;
    color: black; }

